.menu-btn {
  width: 44px;
  height: 44px;
  position: fixed;
  background-color: #0271bc;
}

.page_hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-width: 100vw;
  min-height: 100vh;
  color: #ffffff;
  background-image: url(../images/cover.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.hero_title {
  font-family: "Roboto Slab", serif;
  font-size: 42px;
  text-transform: uppercase;
  line-height: 55px;
  font-weight: bold;
}

.hero_subtitle {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 400;
}

.ball_container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 4%;
  right: 44%;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background-color: #ffffff;
}
